export default {
	data() {
		return {
			message: '',
			baseUrl: '',
			userToken: false
		};
	},
	onLoad() {
		let token = uni.getStorageSync('storage_token');
		if (token == '' || token == null || token == undefined) {
			this.userToken = false;
		} else {
			this.userToken = true;
		}
	},
	onShow() {
		uni.setNavigationBarTitle({
			title: this.$t('index.guanyu')
		});

		this.baseUrl = this.websiteUrl;
		let token = uni.getStorageSync('storage_token');
		console.log(this.baseUrl);
		console.log(token);
		uni.request({
			url: this.baseUrl + '/api/config',
			method: 'get',
			data: {
				type: 'aboutus'
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				token: token
			},
			success: res => {
				console.log(res.data.data);
				this.message = res.data.data;
			},
			fail: () => {},
			complete: () => {}
		});

		
	},
	methods: {
		saveimg() {
			uni.showActionSheet({
				itemList: ['保存到相册'],
				success: res => {
					if (res.tapIndex == 0) {
						uni.showLoading({
							title: '保存中...'
						});
						setTimeout(function() {
							uni.hideLoading();
						}, 6000);
						uni.downloadFile({
							url: 'http://wangsw.aiquanyouxuan.vip/qrcode1.png', //网络路径，下载下来
							success: res => {
								if (res.statusCode === 200) {
									uni.saveImageToPhotosAlbum({
										filePath: 'http://wangsw.aiquanyouxuan.vip/qrcode1.png', //下载后的临时路径
										success: res => {
											//下载完成后在相册里压根找不到
											uni.hideLoading();
											uni.showToast({
												title: '保存成功！'
											});
										}
									});
								}
							}
						});
					}
				}
			});
		},
		saveimgs() {
			uni.showActionSheet({
				itemList: ['保存到相册'],
				success: res => {
					if (res.tapIndex == 0) {
						uni.showLoading({
							title: '保存中...'
						});
						setTimeout(function() {
							uni.hideLoading();
						}, 6000);
						uni.downloadFile({
							url: 'http://wangsw.aiquanyouxuan.vip/qrcode2.png', //网络路径，下载下来
							success: res => {
								if (res.statusCode === 200) {
									uni.saveImageToPhotosAlbum({
										filePath: 'http://wangsw.aiquanyouxuan.vip/qrcode2.png', //下载后的临时路径
										success: res => {
											//下载完成后在相册里压根找不到
											uni.hideLoading();
											uni.showToast({
												title: '保存成功！'
											});
										}
									});
								}
							}
						});
					}
				}
			});
		},
		go_login() {
			uni.navigateTo({
				url: '../login/login'
			});
		}
	}
};
